@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,menu,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,main,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block}*[hidden]{display:none}body{line-height:1}menu,ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{box-sizing:border-box}html,body,#root{height:100%}body{font-family:"Roboto", sans-serif}

.app{width:100%;height:100%;display:flex}

.home{width:100%;height:100%;display:flex}.home__content{flex:1 1}.home__slider{flex:1 1}@media screen and (max-width: 1023px){.home__slider{display:none}}

.home-content{padding:32px 64px;height:100%;display:flex;flex-direction:column;align-items:stretch;justify-content:space-between}.home-content__header{display:flex;align-items:center;justify-content:space-between}.home-content__header-logo{color:#0ec261;font-style:normal;font-weight:bold;font-size:30px;line-height:35px}.home-content__header-city{display:flex;align-items:center;justify-content:space-between;font-style:normal;font-weight:normal;font-size:14px;line-height:16px;color:#999}.home-content__header-city p{margin-left:10px}.home-content__main-title{font-style:normal;font-weight:bold;font-size:70px;line-height:66px;color:#121212;margin-bottom:4px}.home-content__main-subtitle{font-style:normal;font-weight:bold;font-size:70px;line-height:66px;color:#0ec261;margin-bottom:34px}.home-content__main-description{font-style:normal;font-weight:300;font-size:26px;line-height:30px;color:#999;margin-bottom:60px}.home-content__main-button{max-width:250px}.home-content__footer{display:flex;align-items:center;justify-content:space-between}.home-content__footer-company{font-style:normal;font-weight:normal;font-size:13px;line-height:15px;color:#999}.home-content__footer-phone{font-style:normal;font-weight:normal;font-size:13px;line-height:15px;color:#121212}@media screen and (max-width: 768px){.home-content{padding:0}.home-content__header{padding:16px;display:flex;flex-direction:column;align-items:flex-end}.home-content__header-logo{margin-bottom:9px}.home-content__main-title{font-style:normal;font-weight:bold;font-size:32px;line-height:100%;color:#121212;margin-bottom:6px;padding:0 16px}.home-content__main-subtitle{font-style:normal;font-weight:bold;font-size:32px;line-height:100%;color:#0ec261;margin-bottom:16px;padding:0 16px}.home-content__main-description{font-style:normal;font-weight:300;font-size:18px;line-height:21px;color:#999999;margin-bottom:32px;padding:0 16px}.home-content__main-button{max-width:100%}.home-content__main-button button{border-radius:0}.home-content__footer{padding:16px;background:#151b1f;display:flex;flex-direction:column-reverse;align-items:flex-end;margin-top:auto}.home-content__footer-company{font-style:normal;font-weight:normal;font-size:13px;line-height:15px;color:#999}.home-content__footer-phone{font-style:normal;font-weight:normal;font-size:13px;line-height:15px;color:#0ec261}}

.button{width:100%;font-weight:600;background:linear-gradient(90deg, #0ec261 2.61%, #039f67 112.6%);background-blend-mode:darken;border-radius:8px;display:flex;align-items:center;justify-content:center;padding:10px 40px;color:#fff;font-size:18px;border:none;outline:none;cursor:pointer}.button:hover{background:linear-gradient(90deg, #0b9249 2.61%, #026d47 112.6%)}.button._success{background:linear-gradient(90deg, #13493f 0%, #0c7b1b 100%);border-radius:4px}.button._success:hover{background:linear-gradient(90deg, #08211c 2.61%, #074d11 112.6%)}.button._info{background:linear-gradient(90deg, #132949 0%, #0c7b67 100%);border-radius:4px}.button._info:hover{background:linear-gradient(90deg, #081221 2.61%, #074d40 112.6%)}.button._danger{background:linear-gradient(90deg, #493013 0%, #7b0c3b 100%);border-radius:4px}.button._danger:hover{background:linear-gradient(90deg, #211508 2.61%, #4d0725 112.6%)}.button._purple{background:linear-gradient(90deg, #281349 0%, #720c7b 100%);border-radius:4px}.button._purple:hover{background:linear-gradient(90deg, #120821 2.61%, #47074d 112.6%)}

.simple-slider{z-index:1;height:100%}.simple-slider .carousel-root,.simple-slider .carousel.carousel-slider,.simple-slider .slider-wrapper.axis-horizontal,.simple-slider .slider.animated{height:100%}.simple-slider .carousel.carousel-slider .control-arrow{width:64px}.simple-slider .carousel.carousel-slider .control-arrow:hover{background-color:rgba(14,194,97,0.2)}.simple-slider .carousel .control-dots .dot.selected,.simple-slider .carousel .control-dots .dot:hover{background-color:#0ec261}.simple-slider .carousel .control-dots .dot{opacity:1}.simple-slider .carousel .control-prev.control-arrow:before{width:9px;height:9px;border-top:3px solid #eeeeee;border-right:3px solid #eeeeee;border-bottom:0;transform:rotate(-135deg)}.simple-slider .carousel .control-next.control-arrow:before{width:9px;height:9px;border-top:3px solid #eeeeee;border-right:3px solid #eeeeee;border-left:0;border-bottom:0;transform:rotate(45deg)}

.slide{height:100%;position:relative;background-size:cover;background-repeat:no-repeat;background-position:center center}.slide__content{text-align:left;position:absolute;top:380px;left:96px}.slide__content-title{font-style:normal;font-weight:500;font-size:40px;line-height:47px;color:#ffffff;margin-bottom:8px}.slide__content-subtitle{max-width:495px;font-style:normal;font-weight:300;font-size:24px;line-height:100%;color:#eeeeee;margin-bottom:32px}.slide__content-button{max-width:164px}

.menu{width:64px;background-color:#151b1f;display:flex;flex-shrink:0;align-items:center;justify-content:space-between;flex-direction:column;z-index:10}.menu__btn{padding:0;margin:0;background-color:transparent;border:none;outline:none;margin-top:32px;color:white}.menu__language{color:#0ec261;margin-bottom:32px}.menu__language._opened{display:none}@media screen and (max-width: 768px){.menu{width:auto;background-color:transparent;position:absolute;left:16px;top:16px;bottom:0}.menu__btn{color:#121212;margin-top:0}}@media screen and (max-width: 576px){.menu__language{display:none}.menu__language._opened{display:block}}

.menu-content{position:absolute;left:64px;right:0;bottom:0;top:0;z-index:3;display:flex}.menu-content__info{flex:1 1;background:#151b1f;display:flex;flex-direction:column;justify-content:center}.menu-content__info-links{display:flex;flex-direction:column;align-items:flex-start;margin-left:64px}.menu-content__info-links span{text-transform:uppercase;font-style:normal;font-weight:500;font-size:32px;line-height:37px;color:#ffffff;cursor:pointer;margin-bottom:30px}.menu-content__info-links span:hover{color:#0ec261}.menu-content__info-icons{display:flex;margin-left:64px}.menu-content__info-icons-item{margin-right:24px}.menu-content__info-icons-item svg{fill:white}.menu-content__info-icons-item svg:hover{cursor:pointer;fill:#0ec261}.menu-content__opacity{flex:1 1;background-color:rgba(0,0,0,0.7)}@media screen and (max-width: 1024px){.menu-content{left:0;background-color:#151b1f}.menu-content__opacity{display:none}}@media screen and (max-width: 576px){.menu-content__info{padding-top:80px;justify-content:flex-start}.menu-content__info-links span{font-style:normal;font-weight:500;font-size:22px;line-height:26px}}

